.app-header{
    background-color: #090909;
    display: flex;
    justify-content: space-between;
    height: 8vh;
    padding: 10px;
    .left-header{
        padding-inline-start: 20px;
        cursor: pointer;
        user-select: none;
        h1{
            font-family: Edu;
            font-size: 1rem;
        }
        align-items: center;
        img{
            width: 50px;
            height: 50px;
        }
        display: flex;
        gap: 20px;
        color: #e9e5e5;
        
    }

    .right-header{
        nav{
            
            .user-icon{
                border-radius: 50%;
                width:30px;

            }
            padding-inline-end: 20px;
            height: 100%;
            display: flex;
            gap: 30px;
            align-items: center;
            user-select: none;

            a{
                line-height: 30px;
                font-size: .8rem;
                text-decoration: none;
                color:  #e9e5e5;
                font-family: Madefor;

                &.active{
                    margin-bottom: -1.5px;
                    border-bottom:1.5px rgba(150, 148, 148, 0.835) solid;
                }


            }

        }
    }

    @media (min-width:600px) {
        .left-header{
            h1{
                font-size: 1.6rem;
            }

        }
        .right-header{
            nav{
                a{
                font-size: 1.2rem;
                }

            }
        }
        
    }

    
}