.home-page{
    overflow-x: hidden;
    position: relative;
    .title{
        text-align: center;
        z-index: 1;
        color: white;
        position: absolute;
        top: 20%;
        left: 50%;
        width: 100%;
        
        transform: translate(-50%,-50%);
        font-family: Madefor;
        text-align: center;
        user-select: none;
        h1{
            font-size: 3rem;
        }
        h2{
            font-size: 1rem;
        }
    }

    .features-container{
        height: 100vh;
        h1{
            color: white;
            font-family: Madefor;
            font-size: 3rem;
            text-align: center;
            padding-top: 100px;
        }
        .features{
            padding-top: 100px;
            display: flex;
            gap: 50px;
            justify-content: center;
            div{
                h3{
                    // font-size: 2rem;
                }
                display: flex;
                gap: 20px;
                text-align: center;
                flex-direction: column;
                align-items: center;
                background-color: grey;
                border-radius: 50%;
                padding: 100px;
             
                font-family: Madefor;
            }
            img{
                // width: 70px;
            }
        }
    }
  .slideshow{
    opacity: 0.4;




    div{
        z-index: 0;
        height: 89.8vh;
        background-position: center;
        background-size: cover;
    }
    


  }

  @media (min-width:200px) {
    .features{
        flex-direction: column;
        align-items: center;

        div{
            width: 30%;
            height: 100px;
            position: relative;
            h4{
                width:150%;
                
            }
            h3{
                position: absolute;
                top: 5%;
            }

            img{
                width: 50px;
            }
            
        }
    }
    
  }
  @media (min-width:900px) {
    .features{
        flex-direction: unset;
        align-items: unset;

        div{
            width: 30%;
            position: relative;
            h4{
            }
            h3{
                position: absolute;
                top: 5%;
            }

            img{
                width: 70px;
            }
            
        }
    }

    
  }

  @media (min-width:1350px) {
    .features{
        div{
            width: 10%;
        }
        
    }
    
  }
}