.user-profile{
    .danger-btn{
        position: fixed;
        right: 5px;
        left: unset;
        bottom: 1%;
        width:25%;
        height: 10%;
    }
    .danger-zone{
        & > :first-child{
            position: absolute;
            right: 0;
            top: 0;
        }
        display: flex;
        gap: 20px;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: rgba(250, 250, 210, 0.268);
        height: 25%;
        width: 25%;
    }
    position: relative;
    h1{
        text-align: center;
        font-family: Madefor;
        color: whitesmoke;
        font-size:2rem;
    }
    height:fit-content;
    .profile-pic-section{
        position: absolute;
        top: 120%;
        left: 50%;
        transform: translate(-50%,-30%);
        img{
            border-radius: 50%;
            
            width: 100px;
            height: 100px;
            cursor: pointer;
        }

        input{
            display: none;

        }

        label{
            height: 200px;
            width: 200px;
            position: relative;

            .upload-hover{
                &.hover{
                    opacity: 1;
                    transition: all 0.4s;
                }
                pointer-events: none;
                transition: all 0.4s;
                opacity: 0;
                border-radius: 50%;
                width: 100px;
                background-color: rgba(0, 0, 0, 0.424);
                position: absolute;
                display: flex;
                justify-content: center;

                img{
                    border-radius: 0;
                    width: 50px;
                }
            }
        }


    }

    .user-details{
        position: absolute;
        top: 50%;
        left: 70%;
        transform: translate(-70%,-50%);
        width: 100%;
        form{
            width: 100%;
            font-family: Madefor;
            display: flex;
            flex-direction: column;
            gap: 20px;

          input{
            width: 100%;
            padding: 20px;
            border-radius: 30px;
            background-color: grey;

            border: none;
          }  

          button{
            &:hover{
                background-color:#866f06 ;
            }
            all: unset;
            background-color: #B19100;
            padding: 20px;
            border-radius: 20px;
          }
        }
    }

    >button{
        position: fixed;
        bottom: 1%;
        left: 1%;
    }
 

    

    .edit-form{
        justify-content: center;
        height: 100%;
        display: flex;
        gap: 20px;
        flex-direction: column;
        align-items: center;
        width: 100%;
        >*:not(button){
            width: 80%;
            outline: 1px white solid;
        }

        input{
            padding: 30px;
            color: white;

        }


    }

    @media (min-width:500px) {
        .profile-pic-section{
            top: 140%;
            img{
                width: 200px;
                height: 200px;
            }
            label{
                .upload-hover{
                    width: 200px;
        
                }
    
            }
        }

        .danger-btn{
            height: 5%;
            width: 10%;
        }
        .edit-form{
            > *:not(button){
                width: 50%;
            }
        }
        
    }


}