.gallery{
    h1{
        color: whitesmoke;
        font-size: 3rem;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-family: Madefor;

    }
    display: flex;
    flex-direction: column;
    height: 89.9vh;
  
    .upload{
        label{
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
        cursor: pointer;

        }
        &:hover{
            background-color: #d6b31a;
            border: dashed 3px black;
            transition: all 0.4s;
        }
        transition: all 0.4s;
        border: 3px rgba(128, 128, 128, 0.439) dashed;
        background-color: rgb(226, 229, 229);
        flex-grow: 1;
        img{
            
           
            width:70px;
        }
        input{
            display: none;
        }

        
        
    }

    .image-gallery{
        justify-content: center;
        display: grid;
        grid-template-columns: repeat(auto-fill,minmax(250px,1fr));
        flex-grow: 8;
        gap: 20px;
        margin-inline-end: 40px;


            .image{
                position: relative;
                height: fit-content;
                img:not(.delete,.duplicate,.share){
                    object-fit: contain;
                    margin: 20px;
        
                        width: 100%;
                        border-radius: 20px;
                }
                .delete{
                    &.hover{
                        opacity: 1;
                        transition: all 0.2s;
                    }
                    transition: all 0.2s;
                    opacity: 0;
                    cursor: pointer;
                    bottom: 9%;
                    right: -5%;
                    position: absolute;
                    width: 30px;
                }
                .share{
                    &.hover{
                        opacity: 1;
                        transition: all 0.2s;
                    }
                    width: 30px;
                    bottom: 9%;
                    right: 7%;
                    cursor: pointer;
                    
                    transition: all 0.2s;
                    opacity: 0;
                    position: absolute;

                }
                .duplicate{
                    &.hover{
                        opacity: 1;
                        transition: all 0.2s;
                    }
                    transition: all 0.2s;
                    opacity: 0;
                    cursor: pointer;
                    bottom: 9%;
                    right: 9%;
                    position: absolute;
                    width: 30px;
                }

      
            
        }
            }

        }

    
